

/* Skeleton loader styles */
.skeleton {
  position: relative;
  overflow: hidden;
  background-color: #f0f0f0;
}

.skeleton__icon {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #e0e0e0;
  margin-right: 15px;
}

.skeleton__text {
  flex-grow: 1;
  height: 16px;
  background-color: #e0e0e0;
  border-radius: 4px;
  margin: 0 15px;
}

/* Gradient animation for skeleton loader */
.skeleton-loader {
  
  background-image: linear-gradient(32deg, rgb(241, 245, 246) 0%, rgb(212, 219, 213) 100%);
  background-size: 150% 150%;
  
  animation: skeleton-loading 1.5s infinite;
}

/* Keyframes for the gradient animation */
@keyframes skeleton-loading {
    0% {
      background-position: 100% 50%;
    }
    50% {
      background-position: 0% 50%;
    }
    100% {
      background-position: 100% 50%;
    }
}

/* Specific styles for different skeleton parts */
.skeleton__icon-complete,
.skeleton__icon-delete {
  background-color: #cdcdcd;
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

.skeleton__text {
  width: 100%;
  height: 16px;
  background-color: #e0e0e0;
  border-radius: 4px;
}
