@import '../globalStyles.css';


form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.6rem;
  background-color: #f8f8f8;
  padding: 32px;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

label {
  font-family: var(--font1);
  font-size: 2.1rem;
  font-weight: 600;
  margin-bottom: 10px;
  color: var(--mainColor);
}

textarea {
  width: 100%;
  height: 100px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  resize: none;
  margin-bottom: 20px;

  font-family: var(--font2);
  font-size: 1.6rem;
}

textarea::placeholder {
  font-family: var(--font2);
  color: var(--textColor_light);
  font-size: 1.6rem;
  opacity: 1; /* Controla la opacidad del placeholder */
}

textarea:focus {
  border-color: #46a0d7;
  outline: none;
  box-shadow: 0 0 5px rgba(70, 160, 215, 0.5);
}

.btn-container {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  gap: 24px;
}

.--mainButton {
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s;
}

.create-button {
  width: 50%;
  margin: 5px 0;
  color: #fff;
}

.create-button:hover {
  background-color: #3490c3;
}

.cancel-button {
  width: 50%;
  margin: 5px 0;
  background-color: #ff6b6b;
  color: #fff;
}

.cancel-button:hover {
  background-color: #ff5a5a;
}
