@import '../globalStyles.css';

.modal-overlay {
  position: fixed; /* Fijo para que siempre esté visible en la pantalla */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Fondo semitransparente */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Asegurarse de que esté por encima de otros elementos */
}


.modal-styles {

  width: 75vw; /* 75% del ancho de la ventana */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  position: relative; /* Para permitir que elementos internos como un botón de cierre estén bien posicionados */
  display: flex;
  flex-direction: column;
  justify-content: space-between;

    margin: 40px 40px;
    padding: 40px 40px;
    background-color: var(--mediumBackground);   
    border-radius: 8px;

}

/* Estilos para un botón de cerrar */
.modal-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 4.8rem;
  cursor: pointer;
}

.modal-styles h2 {
  padding-bottom: 24px;
}
.modal-styles p {
  font-size: 1.8rem;
}