@import '../globalStyles.css';


.input__searchTodo {

    display: block;
    width: 61.8%;
    height: 36px;

    margin: 0 auto;
    padding-left: 10px;
    font-family: var(--font2);
    font-size: 1.6rem;

}

.input__searchTodo::placeholder {
    
    font-size: 1.6rem;

    color: var(--textColor_light); /* Color del placeholder cuando no está enfocado */
    transition: opacity 0.2s ease; /* Transición suave */

}

.input__searchTodo:focus {

    outline-color: var(--mainColor);
    box-shadow: 0px 2px 7px 2px rgba(0, 0, 0, 0.05);

}

.input__searchTodo:focus::placeholder {
    opacity: 0; /* Oculta el placeholder */
}