li {
    list-style: none;
    font-family: var(--font2);
}

.task-Item {
    display: flex;
    flex-direction: row;
    gap: 16px;
    justify-content: space-between;

    background-color: var(--mainWhite);
    border-radius: 6px;
    padding: 16px 32px;
}

.task-Item p {
    width: 90%;
}

.icon-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.icon_interactive-size {
    width: 24px;
    height: 24px;
}

.task-Item__icon-check {
    color: var(--textColor_light);
    cursor: pointer;
}

.task-Item__icon-check:hover {
    color: var(--mainAlternativeColor);
}

.task-Item__icon-check--completed {
    color: var(--mainAlternativeColor);
}

.task-Item__icon-delete {
    color: var(--textColor_light);
    cursor: pointer ;
}

.task-Item__icon-delete:hover {
    color: var(--secondaryFour);
}


.task-Item__text--completed {
    color: var(--textColor_light);
    font-style: italic;
}