@import '../globalStyles.css';

.--mainButton{
    display: flex;
    align-items: center;
    gap: 8px;

    width: auto;
    height: 40px;
    padding: 8px 16px;

    background-color: var(--mainColor);
    color: var(--mainWhite);
    border: none;
    border-radius: 8px;
    margin: 40px auto;

    cursor: pointer;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

}

/* .--mainButton:hover {

    background-color: var(--hoverColor);  Define un color para el hover, por ejemplo, un tono más oscuro 
    color: var(--hoverTextColor);  Opcional: Cambiar el color del texto al hacer hover 

} */


.--mainButton:hover .mainButton--icon {

    transform: rotate(180deg);
    transition: transform 0.3s ease-in-out;

}
.mainButton--icon {

    transition: transform 0.3s ease-in-out;

}

