:root {

--font1 : 'Lora', serif;
--font2 : 'League Spartan', sans-serif;

--mainColor : #466D83;
--mainAlternativeColor : #8AB683;
--secondaryOne : #48787D;
--secondaryTwo : #BBC731;
--secondaryThree : #FDBA0D;
--secondaryFour : #ED5D30;

--textColor : #505D6D;
--textColor_light : #c7cac2;

--lightBackground : #E9ECE3;
--mediumBackground : #E0E1D8;
--darkBackground : #3A4853;

--mainBlack : #1B272E;
--mainWhite : #F4F4F4;

--positiveBoolean : #B3F86F;
--negativeBoolean : #EFA087;

}

h1, h2, h3 {
    font-family: var(--font1);
	font-weight: 700;

	color: var(--mainColor);
	letter-spacing: 0.05em;
	line-height: 135%;
}


h1 {
	font-size: 2.4rem;
}

h2 {
	font-size: 2.1rem;
}

h3 {
    font-size: 1.8rem;
}

button, a {
  font-family: "League Spartan", sans-serif;
  font-size: 1.8rem;
  letter-spacing: 0.03em;
}

ul, ol, li {
  font-size: 1.8rem;
  color: var(--mainColor);
  list-style: none;
}

li {
  line-height: 150%;
}

a {
  line-height: 150%;
  letter-spacing: 0.04em;
  text-decoration: underline;
  color: var(--mainAlternativeColor);
}